body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 100%;
  color: rgba(0, 0, 0, .87);
}

h1 {
  font-size: 1.9rem;
  margin-bottom: 48px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

h2 {
  font-size: 1.7rem;
}

h3 {
  font-size: 1.6rem;
  font-weight: 400;
}

p {
  font-size: 1.3rem;
  line-height: 1.5;
}

li {
  font-size: 1.3rem;
  line-height: 1.5;
}

li + li {
  margin-top: 12px;
}

.container {
  max-width: 850px;
  margin: 0 auto;
  padding: 48px 24px;
  position: relative;
}

.container--large {
  max-width: 1100px;
}

.analyzing {
  background: #f1f1f1;
  position: relative;
  margin-bottom: 72px;
  overflow: hidden;
}

.analyzing p {
  color: #fff;
}

.analyzing .container {
  z-index: 3;
  padding-top: 72px;
  padding-bottom: 72px;
}

.analyzing:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: rgba(2, 72, 128, .49);
}

.analyzing:after {
  content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: block;
    background: url(/images/analyze.png);
    pointer-events: none;
    z-index: 1;
    width: 115%;
    height: 115%;
    background-size: cover;
    background-repeat: no-repeat;
}

.cover {
  background: url('/images/bg4.png');
  background-size: cover;
  position: relative;
}

.cover:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: block;
  background: rgba(0, 0, 0, .2);
}

.cover .container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 300px;
} 

.cover h1,
.cover p {
  color: #fff;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  line-height: 2;
}

.cover p {
  margin-top: 24px;
}

.about__text {
  margin-bottom: 48px;
}

.members-wrapper {
  background: #f7f7f7;
}

.members-wrapper h1 {
  text-align: center;
}

.members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 24px;
  margin: 0 -12px;
}

.member {
  width: calc(25% - 24px);
  padding: 0 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.member__image {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  overflow: hidden;
}

.member__image img {
  width: 150px;
}

.member h3 {
  text-align: center;
}

.member p {
  margin-top: -12px;
  text-align: center;
  font-size: 1rem;
  color: rgba(0, 0, 0, .57);
}

.footer {
  background: #d1d1d1;
  font-size: 1rem;
}

.footer p {
  font-size: 1rem;
}

.footer p:last-child {
  font-size: .8rem;
  color: rgba(0, 0, 0, .57);
}

.features .container {
  padding-top: 0;
}

.features img {
  width: 100%;
}

.footer .container {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
}

@media only screen and (max-width: 950px) {
  .member {
    width: calc(50% - 24px);
  }
}

@media only screen and (max-width: 750px) {
  .cover .container {
    display: block;
    padding-bottom: 0;
  }

  .cover__image {
    padding-left: 0;
  }

  .cover__info {
    padding-right: 0;
  }

  .cover__info,
  .cover__image {
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .member {
    width: calc(100% - 24px);
  }

  .member + .member {
    margin-top: 24px;
  }
}
